.mode-selector {
  padding-bottom: 1ex;
  border-bottom: 1px solid black;
  margin-bottom: 2ex;
}

.Message,.Term {
  display: grid;
  grid-template-columns: .5fr 1.5fr;
  padding-bottom: 10px;
}


.attributes_group {
  grid-column-end: -1;
}
.Attribute {
  display:block;
}
.Attribute > .Identifier:after {
  content: " = "
}


.Placeable > :first-child:before {
  content: "{ ";
  font-family: monospace;
}

.Placeable >  :last-child:after {
  content: " }";
  font-family: monospace;
}

.ExternalArgument > .Identifier:before {
  content: "$";
}
.ExternalArgument {
  font-family: monospace;
}

.flowed-layout .SelectExpression:before, .flowed-layout .SelectExpression:after {
  content: "";
}

.flowed-layout .SelectExpression > :first-child {
  border: 1px solid black;
  padding: 5px;
  border-radius: 5px;
}

.flowed-layout .variants_group {
  display: inline-table;
  vertical-align: middle;
  border-left: 1px solid black;
  border-right: 1px solid black;
  padding: 0 5px;
  border-radius: 5px;
  margin-left: 5px;
}

.flowed-layout .Variant {
  display: table-row;
}
.flowed-layout .Variant > * {
  display: table-cell;
}
.flowed-layout .Variant > :first-child {
  text-align: right;
}
.flowed-layout .Variant > :first-child:after {
  content: "-";
  padding: 0 2px;
}

.syntax-layout .SelectExpression > :first-child:after {
  content: " -> ";
  font-family: monospace;
  direction: ltr;
}

.syntax-layout .variants_group {
  display: block;
  margin-left: 3ex;
}
.Variant {
  display: block;
}
.Variant > :first-child {
  font-family: monospace;
}
.syntax-layout .Variant > :first-child:before {
  content: "[";
}
.syntax-layout .Variant.Variant_default_true > :first-child:before {
  content: "*[";
  margin-left: -1ex;
}
.syntax-layout .Variant > :first-child:after {
  content : "] ";
  font-family: monospace;
}



